



















import Vue from "vue";

export default Vue.extend({
  name: "ProjectDetailsOverlay",
  props: {
    visible: Boolean,
    color: String,
    title: String,
    htmlContent: String,
  },
  data() {
    return {
      previousScrollPosition: 0,
    };
  },
  mounted() {
    if (this.visible) {
      // ÌåùÏóÖÏù¥ Ïó¥Î¶¥ Îïå, Ïä§ÌÅ¨Î°§ ÏúÑÏπòÎ•º Í∏∞Î°ùÌïòÍ≥† ÏµúÏÉÅÎã®ÏúºÎ°ú Ïù¥Îèô
      this.previousScrollPosition = window.scrollY;
      window.scrollTo(0, 0);
    }
  },
  beforeDestroy() {
    if (this.visible) {
      // ÌåùÏóÖÏù¥ Îã´Ìûê Îïå, Ïù¥Ï†Ñ Ïä§ÌÅ¨Î°§ ÏúÑÏπòÎ°ú Î≥µÍµ¨
      window.scrollTo(0, this.previousScrollPosition);
    }
  },
  watch: {
    // visible ÏÜçÏÑ±Ïùò Î≥ÄÍ≤ΩÏùÑ Í∞êÏßÄÌïòÏó¨ Ï≤òÎ¶¨
    visible(newValue) {
      if (newValue) {
        // ÌåùÏóÖÏù¥ Ïó¥Î¶¥ Îïå, Ïä§ÌÅ¨Î°§ ÏúÑÏπòÎ•º Í∏∞Î°ùÌïòÍ≥† ÏµúÏÉÅÎã®ÏúºÎ°ú Ïù¥Îèô
        this.previousScrollPosition = window.scrollY;
        window.scrollTo(0, 0);
      } else {
        // ÌåùÏóÖÏù¥ Îã´Ìûê Îïå, Ïù¥Ï†Ñ Ïä§ÌÅ¨Î°§ ÏúÑÏπòÎ°ú Î≥µÍµ¨
        window.scrollTo(0, this.previousScrollPosition);
      }
    },
  },
});
